/* markdown css
 * 2018-4-26
 */
.markdown-mod {
  max-width: 800px;
  padding: 1rem;
  margin: 1rem 1rem 3rem;
  font-size: 14px;
  color: #333;
  position: relative;
  border: 1px solid #e2e2e2;
  border-radius: 3px;
  -webkit-box-shadow: 3px 4px 0 #f5f5f5;
          box-shadow: 3px 4px 0 #f5f5f5;
  /*  å¼•ç”¨ */
}
.markdown-mod:empty {
  display: none;
}
.markdown-mod h1 {
  font-size: 2rem;
}
.markdown-mod h2 {
  font-size: 1.5em;
}
.markdown-mod h3 {
  font-size: 1.3em;
}
.markdown-mod h1,
.markdown-mod h2,
.markdown-mod h3 {
  padding: 5px 0;
  margin: 0 0 5px;
  background: #fff;
}
.markdown-mod h1:after,
.markdown-mod h2:after,
.markdown-mod h3:after,
.markdown-mod h4:after {
  content: '';
  display: block;
  border-bottom: 1px solid #eee;
}
.markdown-mod ul {
  padding: 0 0 0 2em;
  list-style: inherit;
}
.markdown-mod ul li {
  margin: 3px 0;
}
.markdown-mod p {
  margin: 3px 0;
}
.markdown-mod a {
  color: #09f;
  font-weight: 400;
}
.markdown-mod a:hover {
  text-decoration: underline;
}
.markdown-mod table {
  margin: 1rem 0;
}
.markdown-mod table td,
.markdown-mod table th {
  padding: .4rem .5rem;
  border: 1px solid #ddd;
}
.markdown-mod blockquote {
  padding: 3px 10px;
  border-left: 3px solid #4CAF50;
  background: #eee;
  border-radius: 0 2px 2px 0;
}
code,
kbd,
pre,
samp,
tt {
  font-family: "Roboto Mono", Monaco, courier, monospace;
  font-size: 0.8em;
  background-color: #f8f8f8;
  -webkit-font-smoothing: initial;
  -moz-osx-font-smoothing: initial;
}
code {
  color: #42b983;
  padding: 3px 5px;
  margin: 0 2px;
  border-radius: 2px;
}



