
@charset 'utf-8';
.file-list-box {
  position: relative;
  padding: 1rem;
}
.file-list-box > header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: #fff;
}
.file-list-box > header h1 {
  color: #333;
  font-size: 2rem;
  line-height: 3.5rem;
}
.file-list-box > header h1 span {
  cursor: pointer;
}
.file-list-box main ul.file-list {
  margin: .5rem 0 1rem;
}
.file-list-box main ul.file-list li {
  line-height: 1.6rem;
}
.file-list-box main ul.file-list li svg {
  width: 1.1rem;
  vertical-align: text-top;
}
.file-list-box main ul.file-list li svg path {
  fill: #333;
}
.file-list-box main ul.file-list li a {
  color: #333;
  font-size: 1.2rem;
  cursor: pointer;
}
.file-list-box main ul.file-list li a:hover {
  text-decoration: underline;
}
@media (max-width: 800px) {
.main-app main ul.file-list li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 3.2rem;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    justify-self: center;
}
.main-app main ul.file-list li svg {
    width: 1.5rem;
    margin-right: .5em;
}
.main-app main ul.file-list li a {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 1.3rem;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    vertical-align: middle;
}
}



